'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import supabase from '@/lib/supabaseBrowserClient'
import { motion } from 'framer-motion'
import ProfilePanel from '@/components/profilePanel'

export default function FindPage() {
    const [jobs, setJobs] = useState<any[]>([])
    const [userId, setUserId] = useState<string | null>(null)
    const router = useRouter()

    useEffect(() => {
        loadOtherJobs()
      }, [])

      const getUser = async () => {
        const { data } = await supabase.auth.getUser()
        setUserId(data.user?.id || null)
      }
      getUser()

      const loadOtherJobs = async () => {
        const { data: userData } = await supabase.auth.getUser()
        const email = userData.user?.email
      
        const { data, error } = await supabase
          .from('jobs')
          .select(`
            *,
            claims (
              id,
              claimer_email
            )
          `)
          .neq('poster_email', email) // for FIND page logic
          .order('created_at', { ascending: false })
      
        if (!error && data) {
          const jobsWithClaimStatus = data.map((job) => ({
            ...job,
            claimedByUser: job.claims?.some((claim: { claimer_email: string | undefined }) => claim.claimer_email === email),
          }))
          setJobs(jobsWithClaimStatus)
        }
      }
      
      const handleClaim = async (jobId: string) => {
        const { data: userData } = await supabase.auth.getUser()
        const email = userData.user?.email
        if (!email) return
      
        const { error } = await supabase.from('claims').insert({
          job_id: jobId,
          claimer_email: email,
        })
      
        if (!error) {
          loadOtherJobs()
        }
      }

return (
    <>
      {/* Static background layer â€” cadet gray */}
      <div className="fixed inset-0 z-0" style={{ background: 'rgb(152,183,191)' }} />
  
      <motion.div
        initial={{ x: '100%' }}
        animate={{ x: 0}}
        exit={{ x: '-100%' }}
        transition={{ duration: 0.6 }}
        style={{
          position: 'absolute',
          width: '100vw',
          height: '100vh',
          overflow: 'auto',
          background: 'rgb(241,180,151)'
        }}
        className="z-10"
      >
        <div className="relative">
        {userId && (
            <div className="absolute top-0 right-0 w-80 h-80">
            <ProfilePanel userId={userId} />
            </div>
        )}
        </div>
        <div className="relative px-6 max-w-3xl mx-auto pt-6">
        <h1 className="text-2xl font-bold mb-6 text-center text-white">Available Jobs</h1>
        <div className="space-y-6">
            {jobs.map((job) => (
            <div key={job.id} className="border p-4 rounded bg-white">
                <h3 className="text-lg font-semibold">{job.title}</h3>
                <p className="text-gray-600">{job.description}</p>
                <p className="text-sm text-gray-400 mt-1">
                {job.is_paid ? 'ðŸ’° Paid' : 'ðŸš« Unpaid'}
                </p>
                <p className="text-xs text-gray-500 mt-2">
                Posted by: {job.poster_email?.split('@')[0] || 'unknown'}
                </p>

                {job.claimedByUser ? (
                <button
                    disabled
                    className="mt-4 bg-gray-400 text-white px-4 py-2 rounded cursor-not-allowed"
                >
                    âœ… Claimed
                </button>
                ) : (
                <button
                    onClick={() => handleClaim(job.id)}
                    className="mt-4 bg-black text-white px-4 py-2 rounded hover:bg-gray-800 transition"
                >
                    Claim Job
                </button>
                )}
            </div>
            ))}
        </div>
        </div>

  
          <div
            onClick={() => router.push('/post')}
            className="fixed top-0 left-0 h-screen w-12 bg-[rgb(152,183,191)] hover:w-48 transition-all duration-300 flex items-center justify-center cursor-pointer z-50 group"
          >
            <span className="text-sm text-black opacity-0 group-hover:opacity-100 transition duration-300 -rotate-90 whitespace-nowrap">
              Post â†’
            </span>
          </div>
      </motion.div>
    </>
  )  
}